<template>
  <div style="max-width: 100%;box-sizing: border-box;" class="feedRecordListDetail">
    <myHeader
      :isBack="true"
      backPath="/feedRecordList"
      title="饲喂记录">
    </myHeader>
    <div class="mt4 list-block">
      <mt-cell title="批次号：" :value="batchNumber"></mt-cell>
      <mt-cell title="饲喂日期：" :value="feedDate"></mt-cell>
      <mt-cell title="饲料记录："></mt-cell>
      <div class="detail-item" v-for="(item,index) in getFeedList" :key="index">
        <div class="select-detail">
          <div class="left">
            <span class="left-name">饲料品种：</span>
            <span>{{item.feedType}}</span>
          </div>
          <div class="right">
            <span>包数：{{item.bagNum}}</span>
          </div>
        </div>
      </div>
    </div>
    
  </div>
</template>
<script>
  import myHeader from "@/components/Header";
  import { getfeedRecordDetails } from '@/api/business'
  import { Indicator } from 'mint-ui'
  import {getParam} from '@/utils/wxUtil'

  export default {
    name: "feedRecordListDetail",
    components: {
      myHeader
    },
    data() {
      return {
        batchNumber: "",
        feedDate: "",
        getFeedList: []
      };
    },
    created() {
      this._getfeedRecordDetails();
    },
    methods: {
      _getfeedRecordDetails() {
        Indicator.open({
          text: '加载中...',
          spinnerType: 'fading-circle'
        });
        // const rcordId = this.$route.params.rcordId;
        const rcordId= getParam('rcordId').replace(/%3D/g,'=').trim();
        getfeedRecordDetails(rcordId).then(res => {
          Indicator.close();
          this.batchNumber = res.data.feedList[0].batchName;
          this.feedDate = res.data.feedDate;
          this.getFeedList = res.data.feedList.map((item, index) => {
            return {
              feedType: item.feedName,
              bagNum: item.consumeQty
            }
          });
        }, err => {
          Indicator.close();
        });
      }
    }
  };
</script>
<style lang="scss" scoped>
  .feedRecordListDetail {
    min-height: 100vh;
    background-color: #f8f8f8;
    padding: 0.12rem 0;

    .list-block {
      font-size: 0.16rem;
      background-color: #fff;
    }

    .detail-item {
      padding-left: 0.2rem;
      .select-detail {
        display: flex;
        flex-wrap: nowrap;
        justify-content: space-between;
        align-items: center;
        height: 0.54rem;
        font-size: 0.14rem;
        border-bottom: 1px solid #dddddd;
        padding-right: 0.23rem;
        .left {
          display: flex;
          justify-content: space-between;
          align-items: center;
          .left-name {
            color: #585858;
            padding-right: 0.05rem;
          }
          .img {
            width: 0.1rem;
            height: 0.06rem;
            margin-left: 0.08rem;
            background: url("~@/assets/icon-next.png") no-repeat;
            background-size: 100%;
          }
        }
        .right {
          text-align: right;
          flex-grow: 0.5;
        }
      }
    }
  }


</style>





